import { create } from 'zustand';
// REMOVED: import { data as initialData } from './data'; // Mock data no longer used - database is source of truth
import type { PaymentMethod } from './types';
import { asSystemId, type SystemId } from '@/lib/id-types';

// API sync helper
async function syncToAPI(
  action: 'create' | 'update' | 'delete',
  data: Partial<PaymentMethod>
): Promise<boolean> {
  try {
    const endpoint = action === 'create'
      ? '/api/settings/payment-methods'
      : `/api/settings/payment-methods/${data.systemId}`;
    const method = action === 'create' ? 'POST' : action === 'update' ? 'PATCH' : 'DELETE';
    
    const response = await fetch(endpoint, {
      method,
      headers: { 'Content-Type': 'application/json' },
      body: action !== 'delete' ? JSON.stringify(data) : undefined,
    });
    return response.ok;
  } catch (error) {
    console.error(`[Payment Methods API] ${action} error:`, error);
    return false;
  }
}

interface PaymentMethodState {
  data: PaymentMethod[];
  initialized: boolean;
  add: (item: Omit<PaymentMethod, 'systemId' | 'isDefault'>) => void;
  update: (systemId: SystemId, updatedPolicy: Omit<PaymentMethod, 'isDefault' | 'systemId'>) => void;
  remove: (systemId: SystemId) => void;
  setDefault: (systemId: SystemId) => void;
  loadFromAPI: () => Promise<void>;
}

export const usePaymentMethodStore = create<PaymentMethodState>()(
  (set, get) => ({
      data: [],
      initialized: false,
      add: (item) => {
        const newItem: PaymentMethod = { 
          ...item, 
          systemId: asSystemId(`PM_${Date.now()}`),
          isDefault: get().data.length === 0,
        };
        set((state) => ({ data: [...state.data, newItem] }));
        
        // Sync to API
        syncToAPI('create', newItem).catch(console.error);
      },
      update: (systemId, updatedFields) => {
        set((state) => ({
          data: state.data.map(p => p.systemId === systemId ? { ...p, ...updatedFields } : p),
        }));
        
        // Sync to API
        const updated = get().data.find(p => p.systemId === systemId);
        if (updated) {
          syncToAPI('update', updated).catch(console.error);
        }
      },
      remove: (systemId) => {
        syncToAPI('delete', { systemId }).catch(console.error);
        set((state) => ({
          data: state.data.filter((p) => p.systemId !== systemId),
        }));
      },
      setDefault: (systemId) => {
        set((state) => ({
          data: state.data.map(p => ({ ...p, isDefault: p.systemId === systemId })),
        }));
        
        // Sync all to API (update default status)
        const data = get().data;
        for (const item of data) {
          syncToAPI('update', item).catch(console.error);
        }
      },
      
      loadFromAPI: async () => {
        if (get().initialized) return;
        
        try {
          // NOTE: Settings data is typically small, but using limit for consistency
          const response = await fetch('/api/settings/payment-methods?limit=30');
          if (response.ok) {
            const json = await response.json();
            const data = json.data || json.methods || [];
            if (data.length > 0) {
              set({ data, initialized: true });
            } else {
              set({ initialized: true });
            }
          }
        } catch (error) {
          console.error('[Payment Methods Store] loadFromAPI error:', error);
        }
      },
    })
);
