import { create } from 'zustand';
import { asSystemId, asBusinessId, type SystemId } from '@/lib/id-types';
import type { ProductType } from './types';

const generateId = () => crypto.randomUUID();

// API sync helper
async function syncToAPI(action: 'create' | 'update' | 'delete', data: Partial<ProductType>): Promise<boolean> {
  try {
    const endpoint = action === 'create' ? '/api/settings/product-types' : `/api/settings/product-types/${data.systemId}`;
    const method = action === 'create' ? 'POST' : action === 'update' ? 'PATCH' : 'DELETE';
    const response = await fetch(endpoint, {
      method,
      headers: { 'Content-Type': 'application/json' },
      body: action !== 'delete' ? JSON.stringify(data) : undefined,
    });
    return response.ok;
  } catch (error) {
    console.error(`[Product Type API] ${action} error:`, error);
    return false;
  }
}

interface ProductTypeState {
  data: ProductType[];
  initialized: boolean;
  add: (productType: Omit<ProductType, 'systemId'>) => ProductType;
  update: (systemId: SystemId, updates: Partial<ProductType>) => void;
  remove: (systemId: SystemId) => void;
  findById: (systemId: SystemId) => ProductType | undefined;
  getActive: () => ProductType[];
  loadFromAPI: () => Promise<void>;
}

const rawData = [
  {
    systemId: generateId(),
    id: 'PT001',
    name: 'Hàng hóa',
    description: 'Sản phẩm vật lý có tồn kho',
    isDefault: true,
    isActive: true,
    createdAt: new Date().toISOString(),
  },
  {
    systemId: generateId(),
    id: 'PT002',
    name: 'Dịch vụ',
    description: 'Dịch vụ không có tồn kho',
    isActive: true,
    createdAt: new Date().toISOString(),
  },
  {
    systemId: generateId(),
    id: 'PT003',
    name: 'Digital',
    description: 'Sản phẩm số (ebook, khóa học online...)',
    isActive: true,
    createdAt: new Date().toISOString(),
  },
] as const;

const initialData: ProductType[] = rawData.map((item) => ({
  ...item,
  systemId: asSystemId(item.systemId),
  id: asBusinessId(item.id),
}));

export const useProductTypeStore = create<ProductTypeState>()(
  (set, get) => ({
    data: initialData,
    initialized: false,
    
    add: (productType) => {
      const newProductType: ProductType = {
        ...productType,
        systemId: asSystemId(generateId()),
        createdAt: new Date().toISOString(),
        isDeleted: false,
      };
      set((state) => ({ data: [...state.data, newProductType] }));
      syncToAPI('create', newProductType).catch(console.error);
      return newProductType;
    },
    
    update: (systemId, updates) => {
      set((state) => ({
        data: state.data.map((item) =>
          item.systemId === systemId
            ? { ...item, ...updates, updatedAt: new Date().toISOString() }
            : item
        ),
      }));
      const updated = get().data.find(i => i.systemId === systemId);
      if (updated) syncToAPI('update', updated).catch(console.error);
    },
    
    remove: (systemId) => {
      set((state) => ({
        data: state.data.map((item) =>
          item.systemId === systemId
            ? { ...item, isDeleted: true, updatedAt: new Date().toISOString() }
            : item
        ),
      }));
      syncToAPI('delete', { systemId }).catch(console.error);
    },
    
    findById: (systemId) => {
      return get().data.find((item) => item.systemId === systemId && !item.isDeleted);
    },
    
    getActive: () => {
      return get().data.filter((item) => !item.isDeleted && item.isActive !== false);
    },
    
    loadFromAPI: async () => {
      if (get().initialized) return;
      try {
        // NOTE: Settings data is typically small, but using limit for consistency
        const response = await fetch('/api/settings/product-types?limit=50');
        if (response.ok) {
          const json = await response.json();
          const data = json.data || [];
          if (data.length > 0) {
            set({ data: data.map((item: any) => ({
              ...item,
              systemId: asSystemId(item.systemId),
              id: asBusinessId(item.id),
            })), initialized: true });
          } else {
            set({ initialized: true });
          }
        }
      } catch (error) {
        console.error('[Product Type Store] loadFromAPI error:', error);
      }
    },
  })
);
